import React from 'react';
import './Contact.css'; // CSS

const Contact = () => {
  return (
    <div className="contact-container">
      <h1 className="contact-title"></h1>
      <form className="contact-form">
        {/* Field */}
        <Field label="" type="text" placeholder="" />
        <Field label="" type="email" placeholder="" />
        <Field label="" type="phone" placeholder="" />
        <Field label="" type="textarea" placeholder="" />
        <button type="submit" className="submit-button"></button>
      </form>
    </div>
  );
};

const Field = ({ label, type, placeholder }) => {
  return (
    <div className="field-container">
      <label className="field-label">{label}</label>
      {type === "textarea" ? (
        <textarea className="text-input" placeholder={placeholder}></textarea>
      ) : (
        <input type={type} className="text-input" placeholder={placeholder} />
      )}
    </div>
  );
};

export default Contact;
