import React from 'react';
import { Link } from 'react-router-dom';  //  React Router
import 'bootstrap/dist/css/bootstrap.min.css';
import { Nav, Navbar as BootstrapNavbar, NavDropdown } from 'react-bootstrap';
import './Navbar.css'




const Navbar = () => {
    return (
        <BootstrapNavbar bg="light" expand="lg">
            <div className="container">
                <BootstrapNavbar.Brand as={Link} to="/"></BootstrapNavbar.Brand>
                <BootstrapNavbar.Toggle aria-controls="navbarNav" />
                <BootstrapNavbar.Collapse id="navbarNav">
                    <Nav className="mr-auto">
                        <NavDropdown title="" id="servicesDropdown">
                            <NavDropdown.Item as={Link} to="/DataFinTech"> FinTech</NavDropdown.Item>
                            <NavDropdown.Item as={Link} to="/SecondMortgagePage"> </NavDropdown.Item>
                            <NavDropdown.Item as={Link} to="/LoanConsuit"> </NavDropdown.Item>
                            <NavDropdown.Item as={Link} to="/ESGtech"> </NavDropdown.Item>
                        </NavDropdown>
                        <Nav.Link as={Link} to="/support-center"></Nav.Link>
                        <NavDropdown title="" id="AboutDropdown">
                            <NavDropdown.Item as={Link} to="/company"> </NavDropdown.Item>
                            <NavDropdown.Item as={Link} to="/BrandStory"> </NavDropdown.Item>
                        </NavDropdown>
                        <Nav.Link as={Link} to="/contact-us"></Nav.Link>
                    </Nav>
                </BootstrapNavbar.Collapse>
            </div>
        </BootstrapNavbar>

    );
}

export default Navbar;