import React, { useState } from 'react';
import './Support.css';

const FAQItem = ({ question, answer }) => {
  const [isActive, setIsActive] = useState(false);

  const toggleIsActive = () => {
    setIsActive(!isActive);
  };

  return (
    <div className={`faq-item ${isActive ? 'active' : ''}`} onClick={toggleIsActive}>
      <h3 className="faq-question">{question}</h3>
      <p className={`faq-answer ${isActive ? 'show' : ''}`}>{answer}</p>
    </div>
  );
};

const Support = () => {
  const faqData = [
    { question: 'Q?', answer: '20-70' },
    { question: 'Q?', answer: ' ' },
    //  FAQ ...
  ];

  return (
    <div className="support-container">
      <h1 className="support-title"></h1>
      <input type="text" placeholder="..." className="support-search" />
      <div className="support-content">
        <h2></h2>
        <div className="faq-section">
          {faqData.map((faq, index) => (
            <FAQItem key={index} question={faq.question} answer={faq.answer} />
          ))}
        </div>
      </div>
      <h2></h2>
      <p></p>
      <div className="contact-section">
        <form>
          <input type="text" placeholder="" required />
          <input type="email" placeholder="" required />
          <input type="phone" placeholder="" required />
          <textarea placeholder="" rows="4" required></textarea>
          <button type="submit" className="submit-button"></button>
        </form>
      </div>
    </div>
  );
};

export default Support;
