import React from 'react';
import './LoanConsuit.css'; // CSS
import { Link } from 'react-router-dom';


import ModelIcon from '../../pictures/WT/icon//algorithm.png'; 
import ReportIcon from '../../pictures/WT/icon//report.png'; 
import EsgIcon from '../../pictures/WT/icon//corporate.png'; 
import EmissionsChart from './EmissionsChart';

const FlexContainer = () => {

  return (
    <div className="content-container">
      <div className="flex-container services-row">
        <div className="service-item">
          <img src={ModelIcon} alt="" className="service-icon" />
          <h3></h3>
          <p></p>
        </div>
        <div className="service-item">
          <img src={ReportIcon} alt="" className="service-icon" />
          <h3></h3>
          <p></p>
        </div>
        <div className="service-item">
          <img src={EsgIcon} alt="ESG" className="service-icon" />
          <h3> ESG </h3>
          <p> ESG </p>
        </div>
      </div>
    </div>
  );
};



const LoanConsuit = () => {


  return (
      <div className="loan-page">
        <div className="service-title">
            <h2>  </h2>
            <p></p>

        </div>



        <FlexContainer />
        <EmissionsChart />

        {/*  */}
        <div className="buttons-container">
          <Link to="/" className="button-link back-home">
            <button></button>
          </Link>
          <Link to="/contact-us" className="button-link contact-us">
            <button></button>
          </Link>
        </div>

      </div>
    )
}

export default LoanConsuit;

