import React from 'react';
import { useNavigate } from 'react-router-dom';

import Slider from 'react-slick';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import './ServiceSection.css';
import slick1 from '../../pictures/WT/icon//2.png';
import slick2 from '../../pictures/WT/icon//6.png';
import slick3 from '../../pictures/WT/icon//3.jpg';
import slick4 from '../../pictures/WT/icon/ESG/ESG.png';


const SlideItem = ({ icon, title, description, path }) => {
  const navigate = useNavigate();

  const handleClick = () => {
    navigate(path);
  };

  return (
    <div className="image-slider__slider-slide">
      <img src={icon} alt={title} className="lazyload" />
      <div className="image-slider__slider-slide-content">
        <div className="image-slider__slider-slide-text-wrapper">
          <h4 className="image-slider__slider-slide-content-title">{title}</h4>
          <div className="image-slider__slider-slide-content-text">{description}</div>
        </div>
        <button className="button button--primary-spring" onClick={handleClick}>
          <span className="button__text button__text--primary-spring">Learn More</span>
        </button>
      </div>
    </div>
  );
};

const ServiceSection = () => {
  const services = [
    {
      icon: slick1,
      title: 'FinTech',
      description: '(Artificial Intelligence)',
      path: '/DataFinTech', // 

    },
    {
      icon: slick2,
      title: '',
      description: 'ChatGPTAI',
      path: '/SecondMortgagePage',
    
    },
    {
      icon: slick3,
      title: '',
      description: '...',
      path: '/LoanConsuit',
    
    },
    {
      icon: slick4,
      title: '',
      description: '...',
      path: '/ESGtech',
    
    },
    // ......
  ];

  const settings = {
    centerMode: true,
    centerPadding: '300px',
    slidesToShow: 1,
    autoplay: true, // 
    autoplaySpeed: 5000, // 1000
    responsive: [
      {
        breakpoint: 768,
        settings: {
          arrows: false,
          centerMode: true,
          centerPadding: '40px',
          slidesToShow: 3
        }
      },
      {
        breakpoint: 480,
        settings: {
          arrows: false,
          centerMode: true,
          centerPadding: '40px',
          slidesToShow: 1
        }
      }
    ],
    // slick dots
    dots: true,
    dotsClass: 'slick-dots', // 
  };

  return (
    <section className="image-slider">
      <Slider {...settings}>
        {services.map((service, index) => (
          <SlideItem key={index} {...service} />
        ))}
      </Slider>
    </section>
  );
}

export default ServiceSection;