import React from 'react';
import './AboutOur.css';
import serviceIcon1 from '../../pictures/WT/icon//1.jpg'; 
import serviceIcon2 from '../../pictures/WT/icon//3.jpg'; 
import serviceIcon3 from '../../pictures/WT/icon//WHY3.jpg'; 

import { useNavigate } from 'react-router-dom';


function AboutOurItem({ icon, title, description, path }) {

  const navigate = useNavigate();
  
  const handleClick = () => {
    if(path) {
      navigate(path); // 
    }
  };


    return (
      <div className="AboutOurItem">
        <div className="AboutOurInfo">
          <h2>{title}</h2>
          {Array.isArray(description) ? (
            <ol>
              {description.map((item, index) => (
                <li key={index}>{item}</li>
              ))}
            </ol>
          ) : (
            <p>{description}</p>
          )}
          {path && <button className="readMore" onClick={handleClick}>Read More</button>}
        </div>
        <div className="AboutOurGraphic">
          <img src={icon} alt={title} className="AboutOurIcon" />
        </div>
      </div>
    );
  }

  function AboutOur() {
    const services = [
      {
        icon: serviceIcon1, // 
        title: '&',
        description: 'description...',
        path: '/company', // 

      },
      {
        icon: serviceIcon2, // 
        title: '',
        description: [
          ' ',
          '',
          ''
        ],

        path: '/brandstory', // 

      },
      {
        icon: serviceIcon3, // 
        title: '',
        description: [
          '',
          '',
          '',
          ''
        ],

      },
      
      // ......
    ];
  
    return (
      <section className="AboutOur">
        <div className="AboutOurList">
          {services.map((service, index) => (
            <AboutOurItem key={index} {...service} />
          ))}
        </div>
      </section>
    );
  }
  
  export default AboutOur;