import React, { useState } from 'react';
import'./SecondMortgagePage.css'; // CSS
import { Link } from 'react-router-dom';

import HouseIcon1 from '../../pictures/WT/icon/2.png'; 
import HouseIcon2 from '../../pictures/WT/icon/1.png'; 
import HouseIcon3 from '../../pictures/WT/icon/3.png'; 
import HouseIcon4 from '../../pictures/WT/icon/4.png'; 
import HouseIcon5 from '../../pictures/WT/icon/.png'; 
import HouseIcon6 from '../../pictures/WT/icon/6.png'; 

import arrow from '../../pictures/WT/icon/arrow-right.png'; 


const FlexContainer = () => {
  return (
    <div className={"content-container"}>
      <div className="flex-container top-row">
        {/*  */}
        <div className="flex-item small-box">
                                             
        </div>
        <div className="flex-item small-box">
          <p>:</p>
          <ul>
            <li></li>
            <li></li>
          </ul>
        </div>
      </div>

      {/* <div className="flex-container bottom-row"> */}
        {/*  */}
        {/* <div className="flex-item large-box">
          <h3></h3>
          <p></p>
          <div className="highlight">110%</div>
        </div>
        <div className="flex-item large-box">
          <h3></h3>
          <p>1. : </p>
          <p>2. : </p>
          <p>3. : </p> */}

          {/* <div className="highlight">120</div> */}
        {/* </div> */}
      {/* </div> */}
    </div>
  );
};

const ProcessStep = ({ image, title, descriptions }) => {

  return (
    <div className="process-step">
      <img src={image} alt={title} className="process-image" />
      <h3 className="process-step-title">{title}</h3>
      <ul className="process-description-list">
        {descriptions.map((description, index) => (
          <li key={index} className="process-description-item">{description}</li>
        ))}
      </ul>
    </div>
  );
};

const SecondMortgagePage = () => {
  // 
  const [activeService] = useState('second-mortgage');

  const services = {
    'second-mortgage': {
      title: '',
      processSteps: [
        {
          image: HouseIcon1,
          title: '',
          descriptions: [
            '',
            '',
            '()'
          ]
        },
        {
          image: HouseIcon2,
          title: '',
          descriptions: ['']
        },
        {
          image: HouseIcon3,
          title: '',
          descriptions: ['']
        },
        {
          image: HouseIcon4,
          title: '',
          descriptions: ['']
        },
        {
          image: HouseIcon5,
          title: '',
          descriptions: ['']
        },
        {
          image: HouseIcon6,
          title: '',
          descriptions: ['']
        },
      ]
    },

  };
  

  return (
      <div className="second-mortgage-page">
        <div className="service-title">
            <h2>{services[activeService].title}</h2>
        </div>


        <FlexContainer />


        {/*  */}
        <div className="process-images-container">
          {/*  */}
          <div className="process-row">
            {services[activeService].processSteps.slice(0, 3).map((step, index, array) => (
              <React.Fragment key={`top-row-fragment-${index}`}>
               <ProcessStep 
                  image={step.image} 
                  title={step.title} 
                  descriptions={step.descriptions} 
                />
                {/* <div className="process-step">
                  <img src={image} alt={` ${index + 1}`} className="process-image" />
                  <p className="process-description">{services[activeService].processDescriptions[index]}</p>
                </div> */}
                {index < array.length - 1 && ( // 
                  <div className="arrow">
                    <img src= {arrow} alt="" />
                  </div>
                )}
              </React.Fragment>
            ))}
          </div>

          {/*  */}
          <div className="process-row">
           {services[activeService].processSteps.slice(3).map((step, index, array) => (
            <React.Fragment key={`bottom-row-fragment-${index}`}>
              <ProcessStep 
                image={step.image} 
                title={step.title} 
                descriptions={step.descriptions} 
              />
                {/* <div className="process-step">
                  <img src={image} alt={` ${index + 4}`} className="process-image" />
                  <p className="process-description">{services[activeService].processDescriptions[index + 3]}</p>
                </div> */}
                {index < array.length - 1 && ( // 
                  <div className="arrow">
                    <img src= {arrow} alt="" />
                  </div>
                )}
              </React.Fragment>
            ))}
          </div>
        </div>
        
        {/* <div className="service-selection">
            {Object.keys(services).map((serviceKey) => (
            <button
                key={serviceKey}
                onClick={() => setActiveService(serviceKey)}
                className={`service-button ${activeService === serviceKey ? 'active' : ''}`}
            >
                {services[serviceKey].title}
            </button>
            ))}
        </div> */}

        {/*  */}
        <div className="buttons-container">
          <Link to="/" className="button-link">
            <button></button>
          </Link>
          <Link to="/contact-us" className="button-link">
            <button></button>
          </Link>
        </div>

      </div>
    )
}

export default SecondMortgagePage;

